.footer {
  width: 100%;
  height: 100px;
  text-align: center;
  background-color: #f7f7f7;
  margin-top: auto;
  padding-top: 28px;
  padding-bottom: 28px;
}

@media screen and (min-width: 768px) {
  .footer {
    height: 79px;
    padding-top: 30px;
